import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import {
  ModalHeader,
  ModalFooter,
  ModalBody,
  Modal
} from 'reactstrap';

import Button from './Button';

import { COLORS } from '../../constants';

class CustomModal extends PureComponent {

  onClose = () => {

  };

  renderHeader = () => {
    const { header, onClose } = this.props;

    if (header) {
      return (
        <ModalHeader toggle={onClose}>
          {header}
        </ModalHeader>
      );
    }

    return null;
  }

  render() {
    const {
      successLabel,
      isOpen,
      header,
      body,
      onClose,
      onSuccess
    } = this.props;

    return (
      <Modal centered isOpen={isOpen} toggle={onClose}>
        { this.renderHeader() }
        <ModalBody>
          {body}
        </ModalBody>
        <ModalFooter>
          <Button
            label="Cancel"
            bgColor={COLORS.GREY}
            onClick={onClose}
          />
          {' '}
          <Button
            label={successLabel}
            onClick={onSuccess}
          />
        </ModalFooter>
      </Modal>
    );
  }
}

CustomModal.propTypes = {
  header: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.string
  ]),
  body: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.string
  ]),
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onSuccess: PropTypes.func.isRequired,
  successLabel: PropTypes.string.isRequired,
};

CustomModal.defaultProps = {
  header: '',
  body: ''
};

export default CustomModal;



