import { delay, takeLatest } from 'redux-saga';
import { put } from 'redux-saga/effects';

import {
  startLayoutSpinner,
  stopLayoutSpinner
} from '../redux/spinners/actions';

import {
  FETCH_USERS_REQUESTED
} from '../redux/users/constants';

const fetchUsers = function* ({ payload }) {
  try {
    yield put(startLayoutSpinner());
    yield delay(1500);
    yield put(stopLayoutSpinner());
  } catch (ex) {
    // TODO
  }
};

export default [
  takeLatest(FETCH_USERS_REQUESTED, fetchUsers),
];



