import React, { Component, Fragment } from 'react';
import { Col, Row, Label } from 'reactstrap';
import { Field } from 'redux-form';
import PropTypes from 'prop-types';
import uuid from 'uuid';

import {
  Accordion,
  Checkbox,
  Input,
  Modal,
  Card
} from '../UI';

import { COLORS } from '../../constants';

const list1 = [
  '',
  '',
  ''
];
const list2 = [
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
  ', ,   ',
];

const Records = () => (
  <Fragment>
    <Col>
      <Row>
        <Label style={{ color: COLORS.GREY, fontSize: 11 }}>
              
        </Label>
      </Row>
      {
        list1.map(cx => (
          <Row key={uuid.v4()}>
            <Checkbox
              labelStyle={{ fontSize: 11, fontWeight: 600 }}
              label={cx}
              onChange={() => {}}
            />
          </Row>
        ))
      }
      <hr />
      <Row>
        <Label style={{ color: COLORS.GREY, fontSize: 11 }}>
            
        </Label>
      </Row>
      {
        list2.map(cx => (
          <Row key={uuid.v4()}>
            <Checkbox
              labelStyle={{ fontSize: 10 }}
              label={cx}
              onChange={() => {}}
            />
          </Row>
        ))
      }
    </Col>
  </Fragment>
);

const dummy = [
  {
    label: '',
    component: <Records />
  },
  {
    label: '',
    component: <p></p>
  },
  {
    label: '',
    component: <p></p>
  },
  {
    label: ' 2',
    component: <p> 2</p>
  }
];

class Roles extends Component {
  state = {
    isModalOpen: false
  }

  onToggleModal = () => {
    this.setState(prevState => ({ isModalOpen: !prevState.isModalOpen }));
  };

  onCreateRole = this.props.handleSubmit(values => {
    this.onToggleModal();
    this.props.createRoleRequested(values);
    this.props.reset('createRole');
  });

  renderCardHeader = () => (
    <Fragment>
      <strong>
        
      </strong>
      <div className="card-header-actions">
        <a className="card-header-action btn btn-minimize" style={{ color: COLORS.BLUE }} onClick={this.onToggleModal}>
          <i className="fa fa-plus" style={{ marginRight: 5 }} />
          <strong>
            
          </strong>
        </a>
      </div>
    </Fragment>
  );

  render() {
    return (
      <Fragment>
        <Card header={this.renderCardHeader()} collapsable>
          <Accordion items={dummy} />
        </Card>
        <Modal
          successLabel=""
          header="  "
          isOpen={this.state.isModalOpen}
          body={
            <Field component={Input} label=" " name="role" />
          }
          onClose={this.onToggleModal}
          onSuccess={this.onCreateRole}
        />
      </Fragment>
    );
  }
}

Roles.propTypes = {
  createRoleRequested: PropTypes.func.isRequired,
  reset: PropTypes.func.isRequired
};

export default Roles;



