import {
  FETCH_USERS_SUCCEEDED
} from './constants';

const INITIALE_STATE = [{
  ftname: 'Anton',
  lname: 'Smirnov',
  tname: 'Gennadievich'
}, {
  ftname: 'Max',
  lname: 'Demydenko',
  tname: 'Sergeevich'
}];

const reducer = (state = INITIALE_STATE, action) => {
  switch (action.type) {
    case FETCH_USERS_SUCCEEDED:
      return action.payload;

    default:
      return state;
  }
};

export default reducer;



