import React, { Component } from 'react';
import PropTypes from 'prop-types';
import uuid from 'uuid';
import {
  FormFeedback,
  FormGroup,
  Input,
  Label,
  Col
} from 'reactstrap';

import { COLORS } from '../../constants';

class CustomInput extends Component {
  render() {
    const {
      labelPosition,
      placeholder,
      inputStyle,
      required,
      disabled,
      error,
      label,
      input, // field from redux form
      meta, // field from redux form
      type,
      name,
      onChange,
      onFocus,
      onBlur,
    } = this.props;

    const inputName = name || uuid.v4();

    const reduxError = meta.touched && meta.invalid ? meta.error : '';
    const inputError = error || reduxError;

    if (labelPosition === 'top') {
      return (
        <FormGroup>
          <Label htmlFor={inputName}>
            {required && <span style={{ color: COLORS.RED, marginRight: 2 }}>*</span>}
            {label}
          </Label>
          <Input
            invalid={inputError.length > 0}
            disabled={disabled}
            placeholder={placeholder}
            style={inputStyle}
            name={inputName}
            type={type}
            onChange={onChange}
            {...input}
          />
          {inputError && <FormFeedback>{inputError}</FormFeedback>}
        </FormGroup>
      );
    }

    return (
      <FormGroup row>
        <Col md="4" style={{ paddingRight: 0 }}>
          <Label htmlFor={inputName} style={{ paddingTop: 5 }}>
            {required && <span style={{ color: COLORS.RED, marginRight: 2 }}>*</span>}
            {label}
          </Label>
        </Col>
        <Col xs="12" md="8">
          <Input
            invalid={inputError.length > 0}
            disabled={disabled}
            placeholder={placeholder}
            style={[{ borderColor: COLORS.BLACK }, inputStyle]}
            name={inputName}
            type={type}
            onChange={onChange}
            {...input}
          />
          {inputError && <FormFeedback>{inputError}</FormFeedback>}
        </Col>
      </FormGroup>
    );
  }
}

CustomInput.propTypes = {
  labelPosition: PropTypes.oneOf(['top', 'left']),
  placeholder: PropTypes.string,
  disabled: PropTypes.bool,
  required: PropTypes.bool,
  inputStyle: PropTypes.object,
  label: PropTypes.string,
  error: PropTypes.string,
  input: PropTypes.object,
  meta: PropTypes.object,
  type: PropTypes.string,
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func
};

CustomInput.defaultProps = {
  labelPosition: 'top',
  placeholder: 'Te ',
  required: false,
  disabled: false,
  inputStyle: {},
  label: '',
  input: {},
  meta: {},
  error: '',
  type: 'text',
  onChange: () => {},
  onFocus: () => {},
  onBlur: () => {},
};

export default CustomInput;



