import { combineReducers } from 'redux';
import { reducer as form } from 'redux-form';

import spinners from './spinners/reducer';
import users from './users/reducer';
import auth from './auth/reducer';

export default combineReducers({
  spinners,
  users,
  auth,
  form
});



