import React, { Component, Fragment } from 'react';
import { Col, Row } from 'reactstrap';
import PropTypes from 'prop-types';

import { Card } from '../UI';

import { COLORS } from '../../constants';

import UsersItem from './UsersItem';

class UsersList extends Component {

  onRefresh = () => {
    this.props.fetchUsersRequested({ name: 'Roma' });
  };

  renderCardHeader = () => (
    <Fragment>
      <strong>
           (6)
      </strong>
      <div className="card-header-actions">
        <a className="card-header-action btn btn-minimize" style={{ color: COLORS.BLUE }} onClick={this.onRefresh}>
          <i className="fa fa-refresh" style={{ marginRight: 5 }} />
          <strong>
            
          </strong>
        </a>
      </div>
    </Fragment>
  );

  render() {
    return (
      <Card collapsable header={this.renderCardHeader()}>
        <Row>
          <Col>
            <UsersItem />
          </Col>
          <Col>
            <UsersItem />
          </Col>
        </Row>
      </Card>
    );
  }
}

UsersList.propTypes = {
  fetchUsersRequested: PropTypes.func.isRequired
};

export default UsersList;



