import React, { Component, Fragment } from 'react';
import { Col, Row, Label } from 'reactstrap';
import PropTypes from 'prop-types';

import {
  Checkbox,
  Button,
  Select,
  Input,
  Card
} from '../UI';

import { COLORS } from '../../constants';

class Filters extends Component {
  state = {
    isHiddenRoles: false,
    organization: null,
    isDiactive: false,
    isActive: false,
    isNew: false,
    fname: '',
    lname: '',
    tname: '',
    role: null
  };

  onChangeFirstName = e => {
    this.setState({ fname: e.target.value });
  };

  onChangeLastname = e => {
    this.setState({ lname: e.target.value });
  };

  onChangeThirdname = e => {
    this.setState({ tname: e.target.value });
  };

  onSelectOrganization = data => {
    this.setState({ organization: data ? data.value : null });
  };

  onSelectRole = data => {
    this.setState({ role: data ? data.value : null });
  };

  onToggleActive = () => {
    this.setState({ isActive: !this.state.isActive }, () => {
      this.props.fetchUsersRequested(this.state);
    });
  };

  onToggleDiactive = () => {
    this.setState({ isDiactive: !this.state.isDiactive }, () => {
      this.props.fetchUsersRequested(this.state);
    });
  };

  onToggleNew = () => {
    this.setState({ isNew: !this.state.isNew }, () => {
      this.props.fetchUsersRequested(this.state);
    });
  };

  onToggleHiddenRoles = () => {
    this.setState({ isHiddenRoles: !this.state.isHiddenRoles }, () => {
      this.props.fetchUsersRequested(this.state);
    });
  };

  onApplyFilters = () => {
    this.props.fetchUsersRequested(this.state);
  };

  render() {
    const {
      isHiddenRoles,
      organization,
      isDiactive,
      isActive,
      isNew,
      role
    } = this.state;

    return (
      <Card
        isOpenByDefault
        collapsable
        style={{ marginBottom: 10 }}
        header={(
          <Fragment>
            <strong>
               
            </strong>
            <div style={{ display: 'inline-block', float: 'right' }}>
              <span>   </span>
              <i className="icon-arrow-down" style={{ marginLeft: 10, fontSize: 10, color: COLORS.BLUE }} />
              <strong style={{ marginLeft: 5, color: COLORS.BLUE }}>50</strong>
            </div>
          </Fragment>
        )}
      >
        <Row>
          <Col md="8" lg="8">
            <Row>
              <Col lg="8" md="8" sm="12" xs="12">
                <Select
                  label=""
                  value={organization}
                  onSelect={this.onSelectOrganization}
                />
              </Col>
              <Col lg="4" md="4" sm="12" xs="12">
                <Select
                  label=""
                  value={role}
                  onSelect={this.onSelectRole}
                />
              </Col>
            </Row>
            <Row>
              <Col lg="4" md="4" sm="12" xs="12">
                <Input
                  label=""
                  onChange={this.onChangeLastname}
                />
              </Col>
              <Col lg="4" md="4" sm="12" xs="12">
                <Input
                  label="'"
                  onChange={this.onChangeFirstName}
                />
              </Col>
              <Col lg="4" md="4" sm="12" xs="12">
                <Input
                  label=" "
                  onChange={this.onChangeThirdname}
                />
              </Col>
            </Row>
            <Row>
              <Col className="mt-3">
                <Button label="" icon="fa fa-search" onClick={this.onApplyFilters} />
              </Col>
            </Row>
          </Col>
          <Col md="1" lg="1">
            <hr className="vertical-divider" />
          </Col>
          <Col md="3" lg="3">
            <Row noGutters>
              <Label>
                 
              </Label>
            </Row>
            <Row noGutters style={{ marginTop: 5 }}>
              <Checkbox
                label=""
                labelStyle={{ backgroundColor: COLORS.YELLOW }}
                checked={isNew}
                onChange={this.onToggleNew}
              />
            </Row>
            <Row noGutters style={{ marginTop: 5 }}>
              <Checkbox
                label=""
                labelStyle={{ backgroundColor: COLORS.BLUE, color: COLORS.WHITE }}
                checked={isActive}
                onChange={this.onToggleActive}
              />
            </Row>
            <Row noGutters style={{ marginTop: 5 }}>
              <Checkbox
                label=""
                labelStyle={{ backgroundColor: COLORS.LIGHT_GREY }}
                checked={isDiactive}
                onChange={this.onToggleDiactive}
              />
            </Row>
            <Row noGutters>
              <Checkbox
                style={{ marginTop: 15 }}
                label="   "
                checked={isHiddenRoles}
                onChange={this.onToggleHiddenRoles}
              />
            </Row>
          </Col>
        </Row>
      </Card>
    );
  }
}

Filters.propTypes = {
  fetchUsersRequested: PropTypes.func.isRequired
};

export default Filters;



