import { delay, takeLatest } from 'redux-saga';
import { put } from 'redux-saga/effects';

import {
  startLayoutSpinner,
  stopLayoutSpinner
} from '../redux/spinners/actions';

import {
  CREATE_ROLE_REQUESTED
} from '../redux/roles/constants';

const createRole = function* ({ payload }) {
  try {
    yield put(startLayoutSpinner());
    yield delay(1500);
    yield put(stopLayoutSpinner());
  } catch (ex) {
    // TODO
  }
};

export default [
  takeLatest(CREATE_ROLE_REQUESTED, createRole),
];



