import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import uuid from 'uuid';
import {
  FormGroup,
  Label,
  Input
} from 'reactstrap';

import Button from './Button';

class CustoCheckbox extends PureComponent {
  render() {
    const unique = uuid.v4();
    const {
      labelStyle,
      inputStyle,
      checked,
      label,
      style,
      onChange
    } = this.props;


    return (
      <FormGroup check className="checkbox" style={style}>
        <Input
          className="form-check-input"
          checked={checked}
          style={inputStyle}
          type="checkbox"
          id={unique}
          onChange={onChange}
        />
        <Label
          className="form-check-label"
          htmlFor={unique}
          style={labelStyle}
          check
        >
          {label}
        </Label>
      </FormGroup>
    );
  }
}

CustoCheckbox.propTypes = {
  labelStyle: PropTypes.object,
  inputStyle: PropTypes.object,
  checked: PropTypes.bool,
  style: PropTypes.object,
  label: PropTypes.string,
  onChange: PropTypes.func.isRequired,
};

CustoCheckbox.defaultProps = {
  labelStyle: {},
  inputStyle: {},
  checked: false,
  style: {},
  label: ''
};

export default CustoCheckbox;



